/*RANGE*/
for (let e of document.querySelectorAll('input[type="range"].slider-progress')) {
    e.style.setProperty('--value', e.value);
    e.style.setProperty('--min', e.min == '' ? '0' : e.min);
    e.style.setProperty('--max', e.max == '' ? '100' : e.max);
    e.addEventListener('input', () => e.style.setProperty('--value', e.value));
}
/*END RANGE*/

/*MOBILE MENU*/
function showMobileMenu(btn, menu){
    const burgerBtn = document.querySelector(btn);
    const mobileMenu = document.querySelector(menu);

    burgerBtn.addEventListener('click', function(){
        mobileMenu.classList.toggle('show-menu');
    })
}

showMobileMenu('.catalog__mob-btn', '.catalog__box-item:first-child');
showMobileMenu('.menu__mob-btn', '.mobile-menu__list');
/*END MOBILE MENU*/

/*MENU BTN*/
function addClassMenuBtn(menu, item){
    const menuBtn = document.querySelectorAll(item);

    document.querySelector(menu).addEventListener('click', function(event){
        const target = event.target;

        menuBtn.forEach((item) => {
            item.classList.remove('menu__link-active');
            target.classList.add('menu__link-active');
        })
    })
}

addClassMenuBtn(".menu", ".menu__link");
addClassMenuBtn(".mobile-menu", ".mobile-menu__link");
/*END MENU BTN*/


/*ACCORDION*/
const accordionBtn = document.querySelectorAll('.filter__item');

accordionBtn.forEach(function(event){
    event.addEventListener('click', function(){
        this.nextElementSibling.classList.toggle('hidden');
    })
})
